@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&amp;subset=cyrillic&display=swap);
script{position:absolute;width:1px;height:1px;overflow:hidden;clip:rect(0 0 0 0)}:root{--prime-color:      #3c8dbc;--prime-color-5d:   #367fa9;--prime-color-10d:  #307095}*{box-sizing:border-box;padding:0;margin:0}html,body,#app{width:100%;height:100%}body{overflow:hidden;font-family:Roboto, Arial, sans-serif}#app{display:grid;grid-template-rows:40px 1fr;grid-template-columns:1fr}button,select{font:inherit}script{font-size:0}.header{display:flex;justify-content:center;align-items:center;background-color:#3c8dbc;background-color:var(--prime-color)}.header ul{display:flex;align-items:center;position:absolute;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default}.header ul li{display:inline;list-style:none;margin:0 15px}.header--left{left:0}.header--right{right:0}.header button{background-color:transparent;border:0;color:#fff;cursor:pointer}.main{display:grid;grid-template:"data data" 70px "time canvas" 150px "list canvas" auto / 300px auto}.main__data{grid-area:data;display:flex;justify-content:space-around;align-items:center}.main__time{grid-area:time}.main__list{grid-area:list}.main__canvas{grid-area:canvas}.main>*{border:1px solid #ccc}

